*, :after, :before {
  box-sizing: inherit;
}

body, ul, h1, h2, h3, p {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

body {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__link {
  display: flex;
}

.header__img {
  width: 40px;
  height: 40px;
}

.nav__list {
  align-items: center;
  font-weight: 500;
  display: flex;
}

@media (width <= 826px) {
  .nav__list {
    visibility: hidden;
  }
}

.nav__item {
  text-transform: uppercase;
  height: 60px;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  line-height: 60px;
  text-decoration: none;
  transition: color .3s;
  display: inline-block;
}

.nav__link:hover {
  color: #00acdc;
}

.nav .is-active {
  color: #00acdc;
  position: relative;
}

.nav .is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  right: 0;
}

.main {
  grid-template-columns: repeat(4, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width <= 1023px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width <= 767px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width <= 487px) {
  .main {
    grid-template-columns: repeat(1, 200px);
  }
}

@media (width <= 319px) {
  .main {
    grid-template-columns: repeat(1, 200px);
  }
}

.product-card {
  box-sizing: border-box;
  color: #060b35;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
  display: flex;
}

.product-card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
}

.product-card__title {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  transition: color .3s;
}

.product-card:hover {
  transform: scale(1.2);
}

.product-card:hover .product-card__title {
  color: #34568b;
}

.product-card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
}

.product-card__rate {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 24px;
  display: flex;
}

.product-card__reviews {
  text-align: center;
  justify-content: center;
  align-items: center;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.product-card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.product-card__price-name {
  color: #616070;
  font-size: 12px;
}

.product-card__price-cost {
  font-size: 16px;
  font-weight: 700;
}

.product-card__button {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  transition: background .3s, color;
}

.product-card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  display: flex;
}

.stars__star {
  background: url("star.822d561e.svg") center no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}
